<template>
    <div>
        <section>
            <base-card v-show="!isLoading">
                <h2>{{ fullName }}</h2>
                <h3> ${{ hourlyRate }}/hour</h3>
            </base-card>
            <div v-if="isLoading">
                <base-spinner></base-spinner>
            </div>
        </section>
        <section>
            <base-card>
                <header>
                    <h2>Interested? Reach out now!</h2>
                    <base-button link :to="contactLink">Contact</base-button>
                </header>
                <router-view></router-view>
            </base-card>
        </section>
        <section>
            <base-card>
                <base-badge :areas="areas" />
                <p>{{ description }}</p>
            </base-card>
        </section>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';


export default {
    props: ['id'],
    computed: {
        coachDetails()
        {
            return this.coaches.find(coach => coach.id === this.id);
        },
        contactLink()
        {
            const route = this.$route.path;
            if (route.includes('/contact')) {
                return route;
            } else {
                return route + '/contact'; //  /coaches/c1/contact
            }
        },
        ...mapGetters(['coaches']),
    },
    created()
    {
        this.loadCoach();
    },
    data()
    {
        return {
            isLoading: false,
            error: null,
            fullName: '',
            hourlyRate: null,
            areas: [],
            description: '',
        };
    },
    methods: {
        setCoach()
        {
            this.fullName = this.coachDetails.firstName + ' ' + this.coachDetails.lastName;
            this.hourlyRate = this.coachDetails.hourlyRate;
            this.areas = this.coachDetails.areas;
            this.description = this.coachDetails.description;
        },
        async loadCoach()
        {
            this.isLoading = true;
            try {
                await this.$store.dispatch('loadCoaches', { forceRefresh: false });
                this.setCoach();
            } catch (error) {
                this.error = error.message || "Something went wrong! Try again later";
                this.$router.push('/404');
            }
            this.isLoading = false;
        },
        handleError()
        {
            this.error = null;
        },
    }
}

</script>
<style scoped>
h3 {
    margin: 0.5rem 0;
}
</style>

